<template>
	<view class="myBill">

		<u-toast ref="uToast" />
	</view>
</template>

<script>
	import {
		billlist
	} from '../../utils/api.js';
	export default {
		data() {
			return {
				page: 1,
				limit: 500
			}
		},
		onShow() {
			let data = {
				nanny_id: uni.getStorageSync('nanny_id'),
				page: this.page,
				limit: this.limit
			}
			this.getbilllist(data)

		},
		methods: {
			getbilllist(data) {

				billlist(data).then(res => {
					if (res.code == 0) {
						console.log(res.data)
						
					} else {
						this.$refs.uToast.show({
							title: res.msg
						})
					}
				})
			},
		}

	}
</script>

<style lang="scss" scoped="">
</style>
